

div#admin-tools{
    font-size: 86%;
    padding: 2px 0 0;
    width: 35%;
    margin: 5px 0 0;
    float: right;
}

div#site-name {
    float: left;
    width: 65%;
    font-size: 76%;
    padding: 13px 0 10px 10px;
}

div#nav-bar{
    clear: both;
    width: 100%;
    height: 25%;
}

ul {
    
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    border: 1px solid #e7e7e7;
    background-color: #f3f3f3;
  }
  
  li {
    float: left;
  }
  
  li a {
    display: block;
    color: #666;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  li a:hover:not(.active) {
    background-color: #ddd;
  }
  
  li a.active {
    color: white;
    background-color: #04AA6D;
  }

  .sidenav {
        height: 600px;
        float: left;
        width: 15%;
        padding: 0;
        background-color: rgb(109, 104, 177);
        z-index: 1;
        
  }
  
  .maincontent {
    height: 600px;
    float: left;
    width: 85%;
    padding: 0;
    background-color: rgb(50, 158, 122);
}

.combo{
    padding: 2%;
}